// base
html,
body,
#root,
.App,
.content_page,
.cover_page {
  height: 100%;
}
body {
  background-color: #050f26;
  overflow: hidden;
}
.init_hide {
  display: none;
}

p {
  color: #acacac;
}

svg {
  width: 50px;
  fill: #808080;
}
svg:hover {
  cursor: pointer;
  fill: rgb(255, 255, 255);
  -webkit-animation: heartbeat 1.5s ease-in-out infinite both;
  animation: heartbeat 1.5s ease-in-out infinite both;
}
